// @ts-nocheck

import { Injectable } from '@angular/core';
import Swal from 'sweetalert2'


interface alert {
    severity: string;
    summary: string;
    detail: string;
}

@Injectable({
  providedIn: 'root'
})
export class MessageService {

  constructor() { }

  add(message: alert) {
    Swal.fire({
      title: `<strong>${message.summary}</strong>`,
      icon: message.severity,
      text: message.detail,
      showCloseButton: true,
      showConfirmButton: false,
      focusConfirm: false,
      toast: true,
      position: "top-right",
      timer: 4000,
      background: '#343e4d',
      color: '#ffffff'
    })
  }
}
