import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/internal/Observable';
import { map, tap } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { LoginForm } from 'src/app/interfaces/login/login-form.interface';
import { TimeUsuario, TimeUsuarioComprobar, Usuario } from 'src/app/interfaces/login/login-respuesta.interface';
// import Swal from 'sweetalert2'

@Injectable({
  providedIn: 'root',
})
export class UsuarioService {
  private API_URL = environment.API_URL;
  usuario?: Usuario;
  ejecutarRefresh: boolean = true;

  constructor(private http: HttpClient, private router: Router) {}

  refreshToken(): Observable<boolean> {
    const refresh_token = localStorage.getItem('refresh_token') || '';
    const url = this.API_URL + `/token/refresh`;

    const refresh = {
      refresh_token: refresh_token,
    };
    return this.http.post(url, refresh).pipe(
      tap((resp: any) => {
        this.guardarStorage(resp.token, resp.refresh_token, resp.data);
      }),
      map((resp) => true)
      // catchError(err => of(false))
    );
  }

  guardarStorage(token: string, refresh_token: string, usuario: Usuario) {
    localStorage.setItem('token', token);
    localStorage.setItem('refresh_token', refresh_token);
    localStorage.setItem('usuario', JSON.stringify(usuario));
  }

  removeStorage() {
    localStorage.removeItem('token');
    localStorage.removeItem('refresh_token');
    localStorage.removeItem('usuario');
    localStorage.removeItem('urldebusqueda');
  }

  login(formdata: LoginForm) {
    // console.log('log-data =>', formdata);
    console.log(formdata);
    // localStorage.setItem('username', formdata)
    const url = this.API_URL + `/login_check`;
    return this.http.post(url, formdata).pipe(
      tap((resp: any) => {
        this.guardarStorage(resp.token, resp.refresh_token, resp.data);
      })
    );
  }

  logout(): void {
    this.removeStorage();
    this.router.navigateByUrl('/auth/login');
  }

  verificarCaducudadToken(): void {
    let token = localStorage.getItem('token')!;

    if (token) {
      let payload = JSON.parse(atob(token!.split('.')[1]));

      let expirado = this.expirado(payload.exp);

      if (expirado) {
        console.log('LOGIN EXPIRADO');
        this.logout();
        return;
      }

      this.verificaRenueva(payload.exp);
    }
  }

  verificaRenueva(fechaExp: number) {
    let tokenExp = new Date(fechaExp * 1000);
    let ahora = new Date();

    ahora.setTime(ahora.getTime() + 30 * 60 * 1000);

    // console.log('Hora del Token', tokenExp);
    // console.log('Hora actual', ahora);

    if (this.ejecutarRefresh) {
      if (tokenExp.getTime() < ahora.getTime()) {
        this.ejecutarRefresh = false;

        this.refreshToken().subscribe(
          (respuesta) => {
            this.ejecutarRefresh = true;
          },
          (err) => {
            this.ejecutarRefresh = true;
            // console.log(err);
            // Swal.fire({
            //   position: 'top-end',
            //   icon: 'error',
            //   title: 'Refresh Token',
            //   html: 'Tenemos problemas al refrescar el token.',
            //   showConfirmButton: false,
            //   timer: 4000
            // })
          }
        );
      }
    }
  }

  expirado(fechaExp: number) {
    // getTime nos llega en milisegundos por eso lo dividimos entre 1000
    let ahora = new Date().getTime() / 1000;
    // console.log(ahora);
    // ahora = ahora + 3730;
    // console.log('ahora = ', ahora);
    // console.log('fechaExp = ', fechaExp);
    // console.log('fechaExp - ahora = ', fechaExp - ahora);
    // console.log('fechaExp < ahora = ',fechaExp < ahora);
    if (fechaExp < ahora) {
      console.log('true');
      return true;
    } else {
      console.log('false');
      return false;
    }
  }

  changePassword(formdata: LoginForm) {
    console.log(formdata);
    const url = this.API_URL + `/users/change-password`;
    return this.http.post(url, formdata);
  }

  // -----------------------------------------------------------------------------------------------

  iniciarTimeUsuario(id: number): Observable<TimeUsuario> {
    const url = this.API_URL + `/time_usuarios_inicio`;
    return this.http.post<TimeUsuario>(url, {id: id});
  }

  pausarTimeUsuario(id: number): Observable<TimeUsuario> {
    const url = this.API_URL + `/time_usuarios_pausa`;
    return this.http.post<TimeUsuario>(url, {id: id});
  }

  comprobarTimeUsuario(id: number): Observable<TimeUsuarioComprobar> {
    const url = this.API_URL + `/time_usuarios_comprobar`;
    return this.http.post<TimeUsuarioComprobar>(url, {id: id});
  }

  
}
