import { HttpClient } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Ubicacion } from '../interfaces/ubicaciones.interface';

@Injectable({
  providedIn: 'root',
})
export class UbicacionesService {
  private readonly http = inject(HttpClient);

  private API_URL = environment.API_URL;

  getUbicaciones(): Observable<Ubicacion[]> {
    const url = this.API_URL + `/ubicaciones`;
    return this.http.get<Ubicacion[]>(url);
  }

  getUbicacion(id: number): Observable<Ubicacion> {
    const url = this.API_URL + `/ubicacion/${id}`;
    return this.http.get<Ubicacion>(url);
  }

  createUbicacion(newUbicacion: Ubicacion): Observable<Ubicacion> {
    const url = this.API_URL + `/ubicacion`;
    return this.http.post<Ubicacion>(url, newUbicacion);
  }

  editUbicacion(newUbicacion: Ubicacion): Observable<Ubicacion> {
    const url = this.API_URL + `/ubicacion`;
    return this.http.put<Ubicacion>(url, newUbicacion);
  }

  deleteUbicacion(id: number): Observable<Ubicacion> {
    const url = this.API_URL + `/ubicacion/${id}`;
    return this.http.delete<Ubicacion>(url);
  }
}
