import { Component, OnInit, OnDestroy, inject, Input, EventEmitter, Output } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuItem } from 'primeng/api';
import { LineaTarea } from 'src/app/interfaces/tarea.interface';
import { MessageService } from 'src/app/services/message.service';
import { PruebasService } from 'src/app/services/pruebas.service';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  selector: 'app-mostrar-tarea-tiempo',
  templateUrl: './mostrar-tarea-tiempo.component.html',
  styleUrls: ['./mostrar-tarea-tiempo.component.scss']
})
export class MostrarTareaTiempoComponent implements OnInit, OnDestroy {
  private readonly tareaService = inject(TareaService);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly messageService = inject(MessageService);
  // private readonly pruebasService = inject(PruebasService);

  cardMenu: MenuItem[];

  @Input() tarea: LineaTarea;
  
  @Input() estadoTareaIniciada!: boolean;

  @Output() cambioEstadoTareaInciada = new EventEmitter<boolean>();

  subcripciones = [];

  ngOnInit(): void {

    this.subcripciones.push(
      this.tareaService.getEstadoTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          console.log(response);
          this.estadoTareaIniciada = response.tareaIniciada;
        },
        error: (error) => {
          this.messageService.add({
            severity: 'error',
            summary: 'Error',
            detail: 'Hubo un error con el estado de la tarea',
          });
        }
      })
    );
  }

  empezarTarea() {
    if (!this.estadoTareaIniciada) {
      this.tareaService.startTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'la tarea a sido iniciada'
          })
          this.cambioEstadoTareaInciada.emit(this.estadoTareaIniciada);
          this.ngOnInit();
        },
        error: (error) => {
          this.messageService.add({
            severity: 'error',
            summary: 'Error',
            detail: 'Hubo un error, no se pudo iniciar la tarea'
          })
        }
      })
    }
  }

  pausarTarea() {
    if (this.estadoTareaIniciada) {
      this.tareaService.pauseTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'la tarea a sido pausada'
          })
          this.cambioEstadoTareaInciada.emit(this.estadoTareaIniciada);
          this.ngOnInit();
        },
        error: (error) => {
          this.messageService.add({
            severity: 'error',
            summary: 'Error',
            detail: 'Hubo un error, no se pudo pausar la tarea'
          })
        }
      })
    }
  }

  detenerTarea() {
    if (this.estadoTareaIniciada) {
      this.tareaService.pauseTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'la tarea a sido pausada'
          })
          this.cambioEstadoTareaInciada.emit(this.estadoTareaIniciada);
          this.ngOnInit();
        },
        error: (error) => {
          this.messageService.add({
            severity: 'error',
            summary: 'Error',
            detail: 'Hubo un error, no se pudo pausar la tarea'
          })
        }
      })
    }
  }


  ngOnDestroy(): void {
    this.subcripciones.forEach((subcripcion) => {
      subcripcion.unsubscribe();
    });
  }
}
