import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, UntypedFormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { PDV, Presupuesto } from 'src/app/interfaces/presupuesto.interface';
import { Producto } from 'src/app/interfaces/producto.interface';
import { MessageService } from 'src/app/services/message.service';
import { PresupuestoService } from 'src/app/services/presupuesto.service';
import { ProductoService } from 'src/app/services/producto.service';
import { PruebasService } from 'src/app/services/pruebas.service';

@Component({
  selector: 'app-pdv',
  templateUrl: './pdv.component.html',
  styleUrls: ['./pdv.component.scss'],
})
export class PdvComponent implements OnInit, OnDestroy {
  // servicios requeridos
  private readonly pruebaService = inject(PruebasService);
  private readonly presupuestoService = inject(PresupuestoService);
  private readonly productoService = inject(ProductoService);
  private readonly activatedRoutes = inject(ActivatedRoute);
  private readonly router = inject(Router);
  private readonly messageService = inject(MessageService);
  private readonly Form = inject(FormBuilder);

  public compuestoSnOptions: Object[] = [
    { value: 'Si', label: 'Si' },
    { value: 'No', label: 'No' },
  ];

  public hadSubmit: boolean = false;

  public loading = false;

  public loadingPrespuestos = true;

  public showPresupuesto = true;
  public showProductos = false;
  public showCantidad = false;

  public productos: Producto[];

  public presupuestos: Presupuesto[] = [];

  public imagenSubida: any;

  public subs: Subscription[] = [];

  public newPDV = this.Form.group({
    presupuesto: [null, Validators.required],
    producto: [null, Validators.required],
    cantidad: [null, Validators.required],
  });

  // form getters ----------------------------------------------------------------------------------

  get presupuesto() {
    return this.newPDV.get('presupuesto') as UntypedFormControl;
  }
  get producto() {
    return this.newPDV.get('producto') as UntypedFormControl;
  }
  get cantidad() {
    return this.newPDV.get('cantidad') as UntypedFormControl;
  }

  // end form getter --------------------------------------------------------------------------------

  ngOnInit(): void {
    this.cargarPresupuestos();
  }

  validar(campo: string) {
    return this.newPDV.get(campo).valid;
  }

  openProductos() {
    this.loading = true;
    this.cargarProductos();
    this.showPresupuesto = false;
    this.showCantidad = false;
    this.showProductos = true;
  }
  openCantidad() {
    this.showProductos = false;
    this.showPresupuesto = false;
    this.showCantidad = true;
  }
  openPresupuesto() {
    this.loading = true;
    this.cargarPresupuestos();
    this.showProductos = false;
    this.showCantidad = false;
    this.showPresupuesto = true;
  }

  selectProducto(idProducto: number) {
    this.producto.setValue(idProducto);
    this.openCantidad();
  }

  submit() {
    this.hadSubmit = true;

    this.loading = true;

    if (this.newPDV.invalid) {
      console.log(this.newPDV.invalid);

      this.loading = false;
      this.messageService.add({
        severity: 'error',
        summary: 'ERROR',
        detail: 'Verifique los campos',
      });
      return;
    }

    const pdvToSend: PDV = {
      presupuesto: this.presupuesto.value,
      producto: this.producto.value,
      cantidad: this.cantidad.value,
    };

    this.reqCreatePdv(pdvToSend);
  }

  // llamadas al back -----------------------------------------------------------------------------

  // cargo los tipos de productos del select homonimo
  cargarPresupuestos() {
    this.subs.push(
      this.presupuestoService.getPresupuestos().subscribe(
        (response) => {
          this.presupuestos = response;
          this.loadingPrespuestos = false;
          this.loading = false;
        },
        (error) => {
          console.log(error);
          this.messageService.add({
            severity: 'error',
            summary: 'ERROR',
            detail: 'Error al cargar presupuestos.',
          });
          return;
        }
      )
    );
  }

  // cargo los tipos de productos del select homonimo
  cargarProductos() {
    this.subs.push(
      this.productoService.getProductos().subscribe(
        (response) => {
          this.productos = response;
          this.loading = false;
          console.log(this.productos);
        },
        (error) => {
          console.log(error);
          this.messageService.add({
            severity: 'error',
            summary: 'ERROR',
            detail: 'Error al cargar Productos.',
          });
          return;
        }
      )
    );
  }

  reqCreatePdv(pdvToSend) {
    console.log(pdvToSend);
    this.subs.push(
      this.presupuestoService.createLineaPresupuesto(pdvToSend).subscribe(
        (response) => {
          console.log(response);
          this.messageService.add({
            severity: 'success',
            summary: 'Exito!',
            detail: 'Pdv registrado con exito!',
          });
          console.log(response);
          this.router.navigateByUrl('/');
        },
        (error) => {
          console.log(error);

          this.loading = false;
          this.messageService.add({
            severity: 'error',
            summary: 'ERROR',
            detail: 'Error al registrar el pdv.',
          });
          return;
        }
      )
    );
  }

  ngOnDestroy(): void {
    this.subs.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }
}
