import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import {
  Mercancia,
  MercanciaPorUbi,
  mercanciaBarcodeResponse,
} from '../interfaces/mercancia.interface';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class MercanciaService {
  constructor(private http: HttpClient) {}

  private API_URL = environment.API_URL;

  getMercanciaFromBarcode(
    codigoEan: string,
  ): Observable<mercanciaBarcodeResponse> {
    const url = this.API_URL + `/producto/verificar_codigo_ean/${codigoEan}`;
    return this.http.get<mercanciaBarcodeResponse>(url);
  }

  getMercanciasByUbicacion(idUbi: number): Observable<MercanciaPorUbi[]> {
    const url = this.API_URL + `/mercancias/ubicacion/${idUbi}`;
    return this.http.get<MercanciaPorUbi[]>(url);
  }

  setMercancia(body: any): Observable<mercanciaBarcodeResponse> {
    const url = this.API_URL + `/producto/cambiar_stock`;
    return this.http.post<mercanciaBarcodeResponse>(url, body);
  }

  createMercancia(newMercancia: Mercancia): Observable<Mercancia> {
    const url = this.API_URL + `/mercancia`;
    return this.http.post<Mercancia>(url, newMercancia);
  }
}
