import { Component, Input, OnDestroy, OnInit, inject } from '@angular/core';
import { cl } from '@fullcalendar/core/internal-common';
import { AutoCompleteCompleteEvent } from 'primeng/autocomplete';
import { Subscription } from 'rxjs';
import {
  Mercancia,
  MercanciaPorUbi,
} from 'src/app/interfaces/mercancia.interface';
import { LineaTarea } from 'src/app/interfaces/tarea.interface';
import { Ubicacion } from 'src/app/interfaces/ubicaciones.interface';
import { MercanciaService } from 'src/app/services/mercancia.service';
import { MessageService } from 'src/app/services/message.service';
import { TareaService } from 'src/app/services/tarea.service';
import { UbicacionesService } from 'src/app/services/ubicaciones.service';

@Component({
  selector: 'app-mostrar-tarea-pre-rec',
  templateUrl: './mostrar-tarea-pre-rec.component.html',
  styleUrls: ['./mostrar-tarea-pre-rec.component.scss'],
})
export class MostrarTareaPreRecComponent implements OnDestroy, OnInit {
  @Input() tarea: LineaTarea;

  private readonly ubicacionesService = inject(UbicacionesService);
  private readonly tareaService = inject(TareaService);
  private readonly mercanciaService = inject(MercanciaService);
  private readonly messageService = inject(MessageService);

  ubicaciones: Ubicacion[] = [];
  mercancias: MercanciaPorUbi[] = [];

  cantidad: number = 0;

  barcode: string = null;
  mercanciaSeleccionada: MercanciaPorUbi = null;

  ubicacionAntiguaSeleccionada: Ubicacion = null;
  ubicacionNuevaSeleccionada: Ubicacion = null;

  public codeInputLength: number = 0;
  public soloNumeros: boolean;
  public codigoValido: boolean = false;
  public cantidadValida: boolean = false;

  public codigoEan: string;
  public filteredMercancias: MercanciaPorUbi[];

  public ubicacionAntigua: string;
  public ubicacionNueva: string;
  public filteredUbicaciones: Ubicacion[];

  subs: Subscription[] = [];

  ngOnInit(): void {
    this.cargarUbicaciones();
  }

  cargarUbicaciones() {
    this.subs.push(
      this.ubicacionesService.getUbicaciones().subscribe({
        next: (res) => {
          console.log(res);
          this.ubicaciones = res;
        },
        error: (error) => {
          console.log(error);
          this.messageService.add({
            summary: 'Error',
            severity: 'error',
            detail: 'Hubo un error al cargar las ubicaciones',
          });
        },
      }),
    );
  }

  cargarMercancias(idUbi: number) {
    this.subs.push(
      this.mercanciaService.getMercanciasByUbicacion(idUbi).subscribe({
        next: (res) => {
          console.log(res);
          this.mercancias = res;
        },
        error: (error) => {
          console.error(error);
          this.messageService.add({
            summary: 'Error',
            severity: 'error',
            detail: 'Hubo un error al cargar las mercancias de la ubicacion',
          });
        },
      }),
    );
  }

  validarTodo() {
    return (
      this.validarUbicaciones() && this.codigoValido && this.cantidadValida
    );
  }

  validarUbicaciones() {
    if (this.ubicacionAntiguaSeleccionada === null || this.ubicacionNuevaSeleccionada === null) {
      return false;
    }
    if (this.ubicacionAntiguaSeleccionada.id === this.ubicacionNuevaSeleccionada.id) {
      return false;
    }
    return true;
  }

  validarCodigo(event) {
    console.log('hey');
    this.barcode = event.target.value;

    let encontrado: boolean = false;

    this.codeInputLength = this.barcode.length;

    const regex = /^[0-9]*$/;
    this.soloNumeros = regex.test(this.barcode);

    console.log('solo numeros = ', this.soloNumeros, ' y 13 caracteres = ', this.codeInputLength);

    this.mercancias.forEach((mercancia) => {
      if (mercancia.codigoEan == this.barcode) {
        this.codigoValido = true;
        this.mercanciaSeleccionada = mercancia;
        encontrado = true;
      }
    });

    if (!encontrado) {
      this.codigoValido = false;
      this.mercanciaSeleccionada = null;
    }
  }

  validarCantidad(event) {
    let cantidad = event.target.value;

    if (cantidad > 0 && cantidad <= this.mercanciaSeleccionada.cantidad) {
      this.cantidadValida = true;
      return;
    }
    this.cantidadValida = false;
  }

  submit() {
    if (!this.validarTodo()) {
      return;
    }

    const body = {
      id: this.tarea.id,
      ubicacionAntigua: this.ubicacionAntiguaSeleccionada.id,
      ubicacionNueva: this.ubicacionNuevaSeleccionada.id,
      mercancia: this.mercanciaSeleccionada.mercanciaId,
      cantidad: this.cantidad,
    };

    console.log(body);

    this.subs.push(
      this.tareaService.registrarPreRec(body).subscribe({
        next: (res) => {
          console.log(res);
          this.messageService.add({
            summary: 'Exito',
            severity: 'success',
            detail: 'Registra de Preparacion / Recepcion realizado con exito',
          });
        },
        error: (error) => {
          console.error(error);
          this.messageService.add({
            summary: 'Error',
            severity: 'error',
            detail: 'Hubo un error al registrar de Preparacion / Recepcion',
          });
        },
      }),
    );
  }

  filter(event: AutoCompleteCompleteEvent, campo: string, array: string) {
    let filtered: any[] = [];
    let query = event.query;

    this[array].forEach((element) => {
      element[campo] = element[campo].toString();
      if (
        element[campo].toLowerCase().indexOf(query.toLowerCase()) == 0 ||
        element[campo]
          .toString()
          .toLowerCase()
          .indexOf(query.toLowerCase()) == 0
      ) {
        filtered.push(element);
      }
    });

    if (array === 'mercancias') this.filteredMercancias = filtered;
    if (array === 'ubicaciones') this.filteredUbicaciones = filtered;
    
  }

  selectMercancia(event: MercanciaPorUbi) {
    this.codigoEan = event.codigoEan;
    this.codeInputLength = this.codigoEan.length;
    this.soloNumeros = true;
    this.codigoValido = true;
    this.mercanciaSeleccionada = this.mercancias.find(
      (mercancia) => mercancia.codigoEan === this.codigoEan,
    );
  }
  clearMercancia() {
    this.codigoEan = null;
  }

  selectUbicacionAntigua(event: Ubicacion) {
    this.ubicacionAntigua = event.ubicacion;
    this.ubicacionAntiguaSeleccionada = this.ubicaciones.find(
      (ubicacion) => ubicacion.ubicacion === this.ubicacionAntigua,
    );
    this.cargarMercancias(this.ubicacionAntiguaSeleccionada.id)
  }
  clearUbicacionAntigua() {
    this.ubicacionAntigua = null;
  }

  selectUbicacionNueva(event: Ubicacion) {
    this.ubicacionNueva = event.ubicacion;
    this.ubicacionNuevaSeleccionada = this.ubicaciones.find(
      (ubicacion) => ubicacion.ubicacion === this.ubicacionNueva,
    );
  }
  clearUbicacionNueva() {
    this.ubicacionNueva = null;
  }

  ngOnDestroy(): void {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
