import {
  Component,
  ElementRef,
  ViewChild,
  OnInit,
  inject,
  OnDestroy,
  Output,
  EventEmitter,
} from '@angular/core';
import { MenuItem } from 'primeng/api';
import { LayoutService } from '../../service/app.layout.service';
import { UsuarioService } from 'src/app/services/auth/usuario.service';
import { Subscription } from 'rxjs';
import { Usuario } from 'src/app/interfaces/login/login-respuesta.interface';
import { Router } from '@angular/router';
import { MessageService } from 'src/app/services/message.service';
import { WindowSizeService } from 'src/app/services/window-size.service';

@Component({
  selector: 'app-topbar',
  templateUrl: './app.topbar.component.html',
})
export class AppTopBarComponent implements OnInit, OnDestroy {
  items!: MenuItem[];

  @ViewChild('menubutton') menuButton!: ElementRef;

  @ViewChild('topbarmenubutton') topbarMenuButton!: ElementRef;

  @ViewChild('topbarmenu') menu!: ElementRef;

  private readonly usuarioService = inject(UsuarioService);
  private readonly messageService = inject(MessageService);
  private readonly router = inject(Router);
  private readonly windowSizeService = inject(WindowSizeService);

  public usuario: any;

  public xs: boolean;
  public sm: boolean;
  public md: boolean;

  public subs: Subscription[] = [];

  public jornadaIniciada!: boolean;
  @Output() estadotjornadaIniciada = new EventEmitter<boolean>();

  constructor(public layoutService: LayoutService) {}

  ngOnInit(): void {
    this.usuario = JSON.parse(localStorage.getItem('usuario'));
    this.subs.push(
      this.usuarioService.comprobarTimeUsuario(this.usuario.id).subscribe({
        next: (response) => {
          console.log(response);
          this.jornadaIniciada = response.jornadaIniciada;
          this.emitir();
        },
        error: (error) => {
          console.log(error);
          this.messageService.add({
            severity: 'error',
            summary: 'Error',
            detail:
              'Hubo un error al obtener la informacion de la jornada, vuelva a logearse',
          });
          this.router.navigateByUrl('/auth/login');
        },
      }),
    );

    this.subs.push(
      this.windowSizeService.windowSize$().subscribe(pantalla => {
        this.xs = pantalla === 'xs';
        this.sm = pantalla === 'sm';
        this.md = pantalla === 'md';
      })
    )
  }

  toogleTarea() {
    if (this.jornadaIniciada) {
      this.subs.push(
        this.usuarioService.pausarTimeUsuario(this.usuario.id).subscribe({
          next: (response) => {
            console.log(response);
            this.jornadaIniciada = false;
            this.messageService.add({
              severity: 'success',
              summary: 'Exito',
              detail: 'La jornada a sido pausada',
            });
            this.emitir();
          },
          error: (error) => {
            console.log(error);
            this.messageService.add({
              severity: 'error',
              summary: 'Error',
              detail: 'Hubo un error al obtener pausar la jornada',
            });
          },
        }),
      );
    } else {
      this.subs.push(
        this.usuarioService.iniciarTimeUsuario(this.usuario.id).subscribe({
          next: (response) => {
            console.log(response);
            this.jornadaIniciada = true;
            this.messageService.add({
              severity: 'success',
              summary: 'Exito',
              detail: 'La jornada a sido iniciada',
            });
            this.emitir();
          },
          error: (error) => {
            console.log(error);
            this.messageService.add({
              severity: 'error',
              summary: 'Error',
              detail: 'Hubo un error al obtener iniciar lajornada',
            });
          },
        }),
      );
    }
  }

  emitir() {
    this.estadotjornadaIniciada.emit(this.jornadaIniciada);
  }

  ngOnDestroy(): void {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
